.slider-label {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.1em;
    align-items: flex-start;
}

.slider-label span {
    font-size: 0.8em;
    color: var(--text-primary);
}

.check-box {
    height: 0;
    width: 0;
    visibility: hidden;
    margin: 0;
}

.slider {
    position: relative;
    background: var(--grey-300);
    cursor: pointer;
    aspect-ratio: 5 / 1; 
    width: 100%;
    border-radius: 15px;
    border: 1px solid var(--grey-400);
    transition: background-color 0.3s ease-in-out;
}

.slider::after {
    content: "";
    position: absolute;
    left: 2px;
    top: 50%;
    transform: translateY(-50%);
    height: 1.2em;
    width: 1.2em;
    border-radius: 50%;
    background: var(--bg-primary);
    box-shadow: var(--dropshadow-sm);
    transition: 0.3s ease-in-out;
}


.check-box:checked + .slider {
    background: var(--success-color);
}

.check-box:checked + .slider::after {
    left: calc(100% - 1.2em - 2px);
}