#popup-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--backdrop-color);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease;
}

.popup-content {
    background: var(--bg-secondary);
    padding: 2rem;
    border-radius: 8px;
    width: clamp(20vw, 50vw, 75vw);
    height: 90vh;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    position: relative;
    animation: scaleIn 0.3s ease;
    z-index: 1001;
    border: 1px solid var(--grey-300);
    box-shadow: var(--dropshadow-lg);
}

.close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    cursor: pointer;
    background: var(--grey-200);
    border: 1px solid var(--grey-300);
    border-radius: 50%;
    box-shadow: var(--dropshadow-sm);
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.close-button:hover {
    background: var(--grey-300);
}

.grouped-mashed {
    display: flex;
    flex-direction: row;
}

.grouped-horizontal {
    display: flex;
    flex-direction: row;
}

#popup-member-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1em;
    width: 100%;
    background: var(--bg-primary);
    box-shadow: var(--dropshadow-sm);
    padding: 1rem;
    border-radius: 15px;
    margin: 0.5rem 0;
    border: 1px solid var(--grey-200);
}

#popup-member-item img {
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    object-fit: cover;
}

#popup-member-item .disabled {
    opacity: 0;
    pointer-events: none;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes scaleIn {
    from {
        opacity: 0;
        transform: scale(0.95);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

body.modal-open {
    overflow: hidden;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: var(--grey-200);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: var(--primary-color);
    border-radius: 25px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: var(--secondary-color);
}