#navigation-docker {
    height: 10vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.7em 1em;
    background-color: rgba(245, 241, 234, 0.8); /* Fallback */
    background: var(--bg-glass);
    box-shadow: var(--dropshadow-md);

    backdrop-filter: var(--blur-effect);
}

#logo {
    position: relative;
    height: 7.5rem;
    width: 12.5rem;
}

#home-search {
    display: flex;
    flex-grow: 0.5;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    gap: 5px;
}

#nav-search {
    width: 0;
    opacity: 0;
    transition: all 0.7s ease;
}
#search-input {
    padding: 0;
    transition: all 0.7s ease;
}

#nav-search.active {
    width: 100%;
    opacity: 1;
}
#nav-search.active #search-input {
    padding: 1em;
}

#settings-menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    gap: 0.5em;
    margin-right: 1em;
}

#profile-picture {
    cursor: pointer;
    width: 3.5em;
    height: 3.5em;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border: 2px solid transparent;
    border-radius: 50%;
    transition: all 0.2s ease;
}

#profile-picture:hover {
    border: 2px solid var(--secondary-color);
    box-shadow: var(--dropshadow-sm);
}

#navigation-docker .nav-button {
    cursor: pointer;
    border: none;
    width: 3.5em;
    height: 3.5em;
    margin-inline: 0.5em;
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--grey-100);
    border: 1px solid var(--grey-100);
    border-radius: 8px;
    transition: all 0.2s ease;
}

#navigation-docker .nav-button:hover {
    background-color: var(--grey-200);
    transform: scale(1.05);
    box-shadow: var(--dropshadow-sm);
}

#navigation-docker #log-out-button {
    background-image: url(../../../assets/images/icons/logout_icon.png);
}

#navigation-docker #settings-button {
    background-image: url(../../../assets/images/icons/settings-icon.png);
}

#navigation-docker #notifications-button {
    background-image: url(../../../assets/images/icons/notifications-icon.png);
}

#navigation-docker #home-button {
    background-size: 60%;
    background-image: url(../../../assets/images/icons/home-icon.png);
}
