
.dropdown {
  position: relative;
  width: 220px;
}


.dropdown-toggle {
  width: fit-content;
  padding: 0.9em;
  background-color: var(--bg-primary);
  margin-top:0;
  color: var(--text-primary);
  border: 1px solid var(--grey-300);
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.2s ease;
}

.dropdown-toggle:hover {
  background-color: var(--grey-200);
  border: 1px solid var(--grey-500);;
}

.arrow {
  transition: transform 0.3s ease;
}
.arrow.open {
  transform: rotate(180deg);
}


.dropdown-content {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  max-height: 25vh;
  overflow-y: auto;
  background-color: var(--bg-secondary);
  backdrop-filter: var(--blur-effect);
  border: 1px solid var(--grey-300);
  border-radius: 8px;
  z-index: 1000;
  box-shadow: var(--dropshadow-lg);
}


.dropdown-item {
  width: 100%;
  padding: 1em 2em;
  background: none;
  border: none;
  text-align: left;
  color: var(--text-primary);
  cursor: pointer;
  font-size: 0.95rem;
  transition: 0.2s ease;
}

.dropdown-item:hover {
  background-color: var(--grey-200);
}

