#side-menu{
    margin-top: 13.2vh;
    left:0;
    display: flex;
    background: var(--bg-glass);
    flex-direction: column;
    width: 18vw ;
    justify-content:left;
    backdrop-filter: var(--blur-effect-heavy);
}

.menu-item{
    width: 100%;
    padding: 20px 25px;
    margin-top: 2.5vh;
    outline: none;
    border: none;
    background: var(--bg-glass);
    box-shadow: var(--dropshadow-medium);
    box-sizing: border-box;
    backdrop-filter: var(--blur-effect);
    color: var(--text-primary);    
    letter-spacing: 0.1em;
    cursor: pointer;
    font-size: 1.1em;
    font-weight: 500;
    transition: 0.25s;
}

.menu-item:hover{
    background-color: var(--primary-600);
    color: var(--text-primary);
}