.center-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.docked-container {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    z-index: 1;
}

.auth-form-container {
    width: 35vw;
    text-align: center;
    padding: 60px 40px;
    border-radius: 10px;
    background: var(--bg-glass);
    box-shadow: var(--dropshadow-md);
    border: 1px solid var(--grey-300);
    backdrop-filter: var(--blur-effect) var(--burn-effect);
    -webkit-backdrop-filter: var(--blur-effect) var(--burn-effect);
}

.page-container {
    display: flex;
    padding: 1vh 0;
    margin-top: 13vh;
    flex-direction: row;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    align-items: flex-start; /* Changed from center to flex-start */
    justify-content: center;
}

.submit-button {
    width: 100%;
    margin-top: 2vh;
    padding: 1.5em 3em;
    outline: none;
    border-radius: 10px;
    background: var(--primary-color);
    box-shadow: var(--dropshadow-sm);
    box-sizing: border-box;
    border: 1px solid transparent;
    color: var(--bg-primary);
    letter-spacing: 0.1em;
    cursor: pointer;
    font-size: 1.1em;
    font-weight: 500;
    transition: 0.25s;
}

.submit-button:hover {
    background: var(--secondary-color);
    color: var(--text-primary);
    box-shadow: var(--dropshadow-md);
}


@media only screen and (max-width: 768px) {
    .auth-form-container {
        width: 50vw;
    }
}

@media only screen and (max-width: 460px) {
    .auth-form-container {
        width: 70vw;
    }

}

/* Cloudy Glass Background Effect */
.cloudy-glass-background {
  position: relative;
  background-color: var(--bg-glass-50);
  backdrop-filter: var(--blur-effect);

}


